<template>
  <!-- EBigTtitle ============================================================== -->
  <div class="EBigTtitle">
    <img
      class="iconBtn"
      src="../../../../public/ydook.jpg"
      alt="ydook transfrom"
      v-on:click="HideLeftBar"
    />
    <br />
    <br />
    <br />
    <br />
  </div>
  <!-- EBigContent ============================================================== -->
  <div class="EBigContent" :style="styleObject">
    <div class="EDLeft" :style="styleObjectL"></div>
    <div class="EDRight" :style="styleObjectR"></div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'EBigContent',
  components: {},
  data () {
    return {
      styleObject: {
        height: '90vh'
      },
      styleObjectL: {
        width: '30vw'
      },
      styleObjectR: {
        width: '30vw'
      },
      isSeenLeftBox: false,
      leftBarWidth: 36
    }
  },
  mounted () {
    this.styleObjectL.width =
      ((document.body.clientWidth - this.leftBarWidth) / 2).toString() + 'px'
    this.styleObjectR.width =
      ((document.body.clientWidth - this.leftBarWidth) / 2).toString() + 'px'
  },
  created () {
    window.onresize = () => {
      return (() => {
        this.TranWidth()
        this.TranHight()
      })()
    }
  },
  methods: {
    TranWidth () {
      this.styleObjectL.width =
        ((document.body.clientWidth - this.leftBarWidth - 1) / 2).toString() +
        'px'
      this.styleObjectR.width =
        ((document.body.clientWidth - this.leftBarWidth - 1) / 2).toString() +
        'px'
    },
    TranHight () {
      this.styleObject.height =
        (document.body.clientHeight - 55).toString() + 'px'
      console.log('this.styleObject.height = ', this.styleObject.height)
    },
    // eslint-disable-next-line
    HideLeftBar() {
      // Jump to EditorPanel
      // console.log('EBigTtitle ===============================================')
      // console.log('EBigTtitle this.isSeenLeftBox = ', this.isSeenLeftBox)
      if (this.isSeenLeftBox) {
        this.isSeenLeftBox = false
        this.$emit('isCLoseL3', true)
        this.leftBarWidth = 36
        this.TranWidth()
      } else {
        this.isSeenLeftBox = true
        this.$emit('isCLoseL3', false)
        this.leftBarWidth = 0
        this.TranWidth()
      }
      this.$emit('isSeenBox3', 0)
      // =================================================================================
      // this.$router.push({ name: 'Home' })
      // this.$emit('isCLoseL3', true)
    }
  }
})
</script>

<style scoped lang="scss">
// ====================================================================
// ====================================================================
// .EBigContent
.EBigContent {
  text-align: left;
  width: 100vw;
  height: 90vh;
  background: rgba(0, 38, 255, 0.6);
  // background: rgb(250, 255, 248);
  background-size: 100% 100%;
}

.EDLeft {
  float: left;
  height: 100%;
  background: rgb(38, 161, 0);
}
.EDRight {
  float: left;
  height: 100%;
  background: rgb(129, 0, 161);
}

// ====================================================================
// ====================================================================
// .EBigTtitle
.EBigTtitle {
  text-align: left;
  width: 100vw;
  height: 35px;
  background: rgba(209, 26, 26, 0.6);
  background-size: 100% 100%;
}

.iconBtn {
  width: 25px;
  height: 25px;
  // background: rgb(116, 219, 102);
  border-radius: 100%;
  margin-top: 5px;
  margin-left: 25px;
  cursor: pointer;
}
// ====================================================================
// ====================================================================
</style>
