
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'EBigContent',
  components: {},
  data () {
    return {
      styleObject: {
        height: '90vh'
      },
      styleObjectL: {
        width: '30vw'
      },
      styleObjectR: {
        width: '30vw'
      },
      isSeenLeftBox: false,
      leftBarWidth: 36
    }
  },
  mounted () {
    this.styleObjectL.width =
      ((document.body.clientWidth - this.leftBarWidth) / 2).toString() + 'px'
    this.styleObjectR.width =
      ((document.body.clientWidth - this.leftBarWidth) / 2).toString() + 'px'
  },
  created () {
    window.onresize = () => {
      return (() => {
        this.TranWidth()
        this.TranHight()
      })()
    }
  },
  methods: {
    TranWidth () {
      this.styleObjectL.width =
        ((document.body.clientWidth - this.leftBarWidth - 1) / 2).toString() +
        'px'
      this.styleObjectR.width =
        ((document.body.clientWidth - this.leftBarWidth - 1) / 2).toString() +
        'px'
    },
    TranHight () {
      this.styleObject.height =
        (document.body.clientHeight - 55).toString() + 'px'
      console.log('this.styleObject.height = ', this.styleObject.height)
    },
    // eslint-disable-next-line
    HideLeftBar() {
      // Jump to EditorPanel
      // console.log('EBigTtitle ===============================================')
      // console.log('EBigTtitle this.isSeenLeftBox = ', this.isSeenLeftBox)
      if (this.isSeenLeftBox) {
        this.isSeenLeftBox = false
        this.$emit('isCLoseL3', true)
        this.leftBarWidth = 36
        this.TranWidth()
      } else {
        this.isSeenLeftBox = true
        this.$emit('isCLoseL3', false)
        this.leftBarWidth = 0
        this.TranWidth()
      }
      this.$emit('isSeenBox3', 0)
      // =================================================================================
      // this.$router.push({ name: 'Home' })
      // this.$emit('isCLoseL3', true)
    }
  }
})
