
import { defineComponent, onMounted } from 'vue'

import RightBox from './RightBox.vue'

export default defineComponent({
  name: 'LeftBox',
  components: {
    RightBox
  },
  data () {
    return {
      seenBox: 0,
      seenBoxSave: 0,
      seenRightBox: true,
      seenLBox: true
    }
  },
  setup () {
    // console.log('Vue3.0 Enter created! LeftBox!')
    onMounted(() => {
      // console.log('Vue3.0 Enter onMounted! LeftBox! in stup!')
    })
  },
  provide () {
    return {
      isSeenBox: this.seenLBox
    }
  },
  methods: {
    closeLefttCont () {
      this.seenBox = 0
      this.seenRightBox = true
    },
    closeLeftCont () {
      this.seenBox = 0
      this.seenRightBox = false
    },
    closeLeftContOnly () {
      if (this.seenBox !== 0) {
        this.seenBox = 0
      } else {
        this.seenBox = this.seenBoxSave
      }
    },
    closeRightCont () {
      this.seenRightBox = false
    },
    Yfun () {
      // `this` 指向该组件实例
      if (this.seenBox !== 1) {
        this.seenBox = 1
      } else {
        this.seenBox = 0
        if (this.seenRightBox === false) {
          this.seenRightBox = true
        }
      }
      this.seenBoxSave = this.seenBox
    },
    Dfun () {
      if (this.seenBox !== 2) {
        this.seenBox = 2
      } else {
        if (this.seenRightBox === false) {
          this.seenRightBox = true
        }
        this.seenBox = 0
      }
      this.seenBoxSave = this.seenBox
    },
    Ofun () {
      if (this.seenBox !== 3) {
        this.seenBox = 3
      } else {
        if (this.seenRightBox === false) {
          this.seenRightBox = true
        }
        this.seenBox = 0
      }
      this.seenBoxSave = this.seenBox
    },
    OOfun () {
      if (this.seenBox !== 4) {
        this.seenBox = 4
      } else {
        if (this.seenRightBox === false) {
          this.seenRightBox = true
        }
        this.seenBox = 0
      }
      this.seenBoxSave = this.seenBox
    },
    Kfun () {
      if (this.seenBox !== 5) {
        this.seenBox = 5
      } else {
        if (this.seenRightBox === false) {
          this.seenRightBox = true
        }
        this.seenBox = 0
      }
      this.seenBoxSave = this.seenBox
    },
    AIfun () {
      if (this.seenBox !== 6) {
        this.seenBox = 6
      } else {
        if (this.seenRightBox === false) {
          this.seenRightBox = true
        }
        this.seenBox = 0
      }
      this.seenBoxSave = this.seenBox
    },
    Rbox () {
      this.seenRightBox = true
      console.log(this.seenRightBox)
    },
    // eslint-disable-next-line
    isSeenBoxFun(item: any) {
      if (item === 0) {
        this.seenBox = 0
        // console.log('isSeenBoxFun this.seenBox = ', this.seenBox)
        // console.log('isSeenBoxFun item = ', item)
      }
    },
    // eslint-disable-next-line
    isCLoseL1Fun(item: any) {
      if (item === false) {
        this.seenLBox = false
        // console.log('LeftBox isCLoseL1 item = ', item)
      } else {
        this.seenLBox = true
        // console.log('LeftBox isCLoseL1 item = ', item)
      }
    }
  }
})
